.subcategory {
  position: absolute;
  border: 1px solid #ebebf0;
  border-radius: 10px;
  background-color: #fff;
  padding: 15px 15px 15px 45px;
  z-index: 1;
  box-shadow: 2px 2px 7px #b3b3b3;
  min-width: 250px;
}

.subcategory__title {
  font-size: 16px;
  line-height: 1.5;
  margin: 0;
}

.subcategory__group {
  border-top: 1px solid #bbb;
  margin-top: 20px;
  padding-top: 15px;
}

@media (max-width: 600px) {
  .subcategory {
    width: 40%;
    left: calc(22% + 15px);
    top: calc(74% + 15px);
  }
}

@media (min-width: 768.1px) {
  .subcategory {
    left: calc(100% + 15px);
    top: calc(100% + 15px);
  }
}

.location-range {
  display: flex;
  flex-direction: column;
  padding: 15px 20px 0 0;
  border-top: 1px solid #ebebf0;
}

.location-range__slider {
  margin-left: 20px;
  min-width: 80%;
  margin-bottom: 20px;
  margin-top: 10px;
}

.location-range__input-group {
  margin-left: 20px;
  width: 50%;
}

.range-input {
  width: 40%;
  border-radius: 8px;
  background-color: rgba(141, 143, 165, 0.16);
  border: none;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 150%;
  letter-spacing: 0.2px;
  color: #555770;
  padding: 5px 0px 5px 10px;
}

.range-input:focus {
  outline: none;
}

.range-label {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 150%;
  letter-spacing: 0.2px;
  color: #555770;
}

.range-label.unit {
  padding-left: 5px;
}

.rc-slider-dot {
  display: none;
}

.rc-slider-handle {
  border: solid 1.5px #000;
  height: 10px;
  width: 10px;
  margin-top: 0;
}

.rc-slider-handle:active {
  border-color: #000;
  box-shadow: 0 0 5px #000;
}

.rc-slider-handle:hover {
  border: solid 1.5px #000;
}

.rc-slider-handle-click-focused:focus {
  border: solid 1.5px #000;
}

.rc-slider-rail {
  height: 10px;
}

.rc-slider-track {
  height: 10px;
  background-color: #000;
}

.rc-slider-step {
  height: 10px;
}

.rc-slider {
  padding-bottom: 20px;
}

.rc-slider-mark-text {
  min-width: 35px;
}

.location-search-container {
  height: 40px;
}

.input-wrapper {
  position: relative;
  height: 100%;
  background: rgba(141, 143, 165, 0.16);
  color: #000;
  border-radius: 8px;
  border: none;
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 150%;
  padding: 0 0 0 10px;
  transition: background 300ms ease;
  overflow: hidden;
}

.input-wrapper:hover {
  background: rgba(202, 202, 202);
}

.input-wrapper span {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translate(0, -50%);
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  color: #28293d;
}

.location-search-container .location-search-input {
  width: 100%;
  height: 100%;
  background: transparent;
  border: none;
  overflow: hidden;
  position: absolute;
  font-family: Inter;
  font-style: normal;
  font-size: 16px;
  padding-right: 110px;
}

.location-search-input:focus {
  outline: none;
}

.suggestion-item {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 150%;
  align-items: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  color: #000;
  padding: 10px;
}

.suggestion-item:hover {
  background-color: #3a5e57;
  color: #fff;
  cursor: pointer;
}

.suggestion-item span {
  margin-right: 20px;
  display: flex;
  align-items: center;
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 150%;
}

.location-search-span {
  padding-left: 10px;
}

.autocomplete-dropdown-container {
  position: absolute;
  top: calc(100% + 5px);
  width: 100%;
  background: #fff;
  box-sizing: border-box;
  border-radius: 15px;
}

.autocomplete-dropdown-container:not(:empty) {
  border-radius: 10px;
}

.pin {
  height: 24px;
  width: 24px;
  margin: 0;
}

/* for smart phones */
@media (max-width: 768px) {
  .location-search-container {
    height: 80%;
    width: 100%;
  }
}

/*for desktop*/
@media (min-width: 768.1px) {
  .suggestion-item {
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
  }
}

.search-bar {
  border-radius: 16px;
  box-shadow: 0px 10px 18px -5px #888;
  align-items: center;
  margin: 0 auto;
  background: #fff;
  position: relative;
  z-index: 5;
}

.wrapper > .search-bar {
  transform: translate(0, -40%);
  margin: auto;
}

.search-bar-container {
  padding-top: 20px;
  display: contents;
}

.search-bar__element {
  position: relative;
}

.search-bar__element-title {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 150%;
  letter-spacing: 0.2px;
  color: #555770;
  margin-bottom: 0;
}

.search-bar__display {
  display: flex;
  position: relative;
  align-items: center;
  width: 100%;
  cursor: pointer;
  border: none;
  background: rgba(141, 143, 165, 0.16);
  border-radius: 8px;
  padding-left: 10px;
  transition: background 300ms ease;
}

.search-bar__element.activities .search-bar__display {
  color: #28293d;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
}

.search-bar__display:hover {
  background: rgb(202, 202, 202);
}

.search-bar__group {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 150%;
  color: #000;
}

.search-bar__display.button {
  color: #fff;
  background: #3a5e57;
  padding: 0;
  border-radius: 4px;
  margin-top: auto;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  display: table-cell;
  vertical-align: middle;
  align-self: center;
  transition: background 300ms ease;
}

.search-bar__display.button:hover {
  background: #3a5e57;
}

.search-bar__display.button:focus {
  outline: none;
}

.search-bar__popup {
  display: flex;
  flex-direction: column;
  padding: 0px;
  position: absolute;
  border: 0px solid #bbb;
  border-radius: 10px;
  background-color: #fff;
  max-height: 0;
  overflow: hidden;
  transition: 0.5s;
  width: 25%;
  box-shadow: 2px 2px 7px #b3b3b3;
}

.search-bar__popup.open {
  max-height: 300px;
  background: #fff;
  margin-top: 10px;
  box-sizing: border-box;
  border-radius: 15px;
  display: block;
  z-index: 1;
}

.suggestion-container {
  overflow: scroll;
  overflow-x: hidden;
  max-height: 200px;
}

.suggestion-item:hover {
  background: #3a5e57;
}

.activity {
  cursor: pointer;
  flex: none;
  margin: 0px 0px;
  height: 46px;
  height: 50px;
  display: flex;
  align-items: center;
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 150%;
  color: #000;
  padding: 0px 10px 0px 10px;
}

.activity:hover,
.activity.clicked {
  background: #3a5e57;
  color: #fff;
}

.arrow-icon {
  border-radius: 50%;
  background: #fff;
  min-width: 28px;
  min-height: 28px;
  border: none;
  margin-left: auto;
  position: relative;
  display: inline-block;
}

.arrow-icon:after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  right: 10px;
  height: 10px;
  width: 10px;
  background: url(/static/media/right-arrow.40ea8240.svg);
  background-size: cover;
  background-repeat: no-repeat;
  transition: transform 300ms ease;
}

.arrow-icon.left {
  transform: rotate(180deg);
  position: absolute;
  left: 10px;
  cursor: pointer;
}

a {
  text-decoration: none;
  display: inline-block;
}

a:hover {
  background-color: #ddd;
  color: black;
}

.next,
.prev {
  background-color: #fff;
  color: #3a5e57;
  padding-left: 5px;
  margin-right: 5px;
  font-weight: bold;
  font-family: Inter;
  line-height: 150%;
  cursor: pointer;
}

.round {
  border-radius: 50%;
  width: 28px;
  height: 28px;

  border: 1px solid #f2f2f5;
  box-sizing: border-box;
}

.fa-map-pin {
  margin-right: 15px;
}



@media (max-width: 600px) {
  .search-bar {
    width: 87%;
    height: 21vh;
    flex-direction: column;
  }

  .search-bar__element {
    width: 90%;
