  }
  .search-bar__container {
    padding: 4% 0 0 0;
    height: 100%;
  }

  .wrapper{
    transform: translate(0, -10%);
  }

  .search-bar__display ,.location-search-container {
    height: 56%;
  }

  .search-bar__display.button {
    width: 20%;
    font-size: 16px;
    height: 16%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1%;
    left: 40%;

  }


  .search-bar__popup {
    width: 100%;
    
  }

}

@media (min-width: 768px) {
  .search-bar {
    width: 70%;
    height: 29vh;
    flex-direction: column;
  }
.wrapper{
  transform: translate(0, -10%);
}
  .search-bar__element {
    width: 90%;
    height: 35%;
    margin-left: auto;
    margin-right: auto;
  }
  .search-bar__container {
    padding: 4% 0 0 0;
    height: 100%;
  }


  .search-bar__display,.location-search-container  {
    height: 56%;
  }

  .search-bar__display.button {
    width: 20%;
    font-size: 16px;
    height: 16%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1%;
    left: 40%;

  }


  .search-bar__popup {
    width: 100%;
    
  }

  .subcategory {
    
    right: 0;
  }
}
@media  (min-width: 992px) {
  .search-bar {
    width: 80%;
    height: 100px;
  }
  .wrapper{
    transform: translate(0, -3%);
  }

  .search-bar__container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 15px 25px 0 15px;
  }

  .search-bar__element {
    height: 80%;
    min-width: 300px;
    width: auto;
  }

  .search-bar__element.activities .search-bar__popup.open {
    margin-top: 15px;
  }

  .search-bar__display {
    height: 40px;
    width: 100%;
  }

  .location-search-container {
    width: 100%;
  }

  .search-bar__display.button {
    width: 20%;
    margin: -1% 0 0 0 ;
    height: 40px;
    left: auto;
    
  }

  .search-bar__popup {
    width: 100%;
  }

}


html {
  font-family: "Inter", sans-serif;
}
@supports (font-variation-settings: normal) {
  html {
    font-family: "Inter var", sans-serif;
  }
}



.recommendations {

    padding-bottom: 65px;
    position: relative;
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 92% auto;

}

.recommendations__cards {
  margin: 16px;
  width: 95%;
  display: grid;
  grid-gap: 3%;
  grid-template-columns: 32.3% 32.3% 32.3%;

  margin: auto;
}

.recommendations__cards .card img:hover {
  opacity: 0.7;
}

.recommendations__title {
  margin: 30px 0 30px 20px;
  font-size: 30px;
  letter-spacing: 0.75px;
  text-transform: uppercase;
  color: #555770;
}

.carousel-container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.card {
  border-radius: 16px;
  background-color: white;
  display: flex;
  flex-direction: column;
  
}



.card__body {
  min-height: 1px;
  height: 100%;
  text-align: justify;
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 16px;
}

.card__body .card__body__title {
  margin: 0px ;
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  text-align: left;
  line-height: 150%;
  color: #000;

}

.card__body .card__body__address,
.card__body__details {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 150%;
  color: #555770;
  padding: 8px 0;
}

.card__body__tags {
  display: inline-block;
}

.card__body__tag {
  font-size: 13px;
  border-radius: 4px;
  background-color: #f2f2f2;
  display: inline-block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 5px;
  padding: 5px;
}

.card__body__btn {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  text-align: right;
  letter-spacing: 0.75px;
  text-transform: uppercase;
  color: #3a5e57;
  transition: color 300ms ease;
  position: absolute;
  right: 16px;
  bottom: 16px;
}

.card__body__btn:hover {
  background: transparent;
  color: #3a5e57;
}

.card__image {
  border-radius: 8px 8px 0 0;
  flex: none;
  margin: 0px 16px;
  width: 100%;
  height: 270px;
  vertical-align: middle;
  margin-right: auto;
  margin-left: auto;
  object-fit: cover;
}

.card__image-holder {
  outline: none;
}

.recommendations .slick-track {
  display: grid;
  grid-auto-flow: column;
}

/*.card__image {
  max-height: 175px;
}*/

.slick-slide{
  width: 100%;
}
.slick-slide.slick-active {
  height: 100%;
  width: 90%;
}

.slick-slide > div {
  height: 100%;
  margin: 5px;
}

.slick-next::before,
.slick-prev::before {
  opacity: unset;
  color: #000;
}



@media (max-width: 768px) {
.recommendations {
  grid-template-rows: 67% 34%;
}

.card__body .card__body__title {
  font-size: 20px; 
