body {
  background: #f2f2f2;
}

.detail-container {
  width: 100%;
  align-items: center;
  max-width: 1140px;
  margin: 80px auto 0 auto;
}

.detail-container__head {
  width: 100%;
  height: 10%;
  position: fixed;
  z-index: 3;
}



.detail-container__body .slick-slide > div {
  height: auto;
}

.detail-container__title {
  grid-area: left;
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 54px;
  margin-top: auto;
  margin-bottom: auto;
}

.detail-image-wrapper {
  padding: 10px;
  height: 400px;
}

.detail-container__body__image {
  object-fit: cover;
  overflow: hidden;
  border-radius: 8px;
  width: 100%;
  height: 100%;
}

.detail-container__body__images {
  margin-left: auto;
  margin-right: auto;
  height: 50%;
  padding: 0 30px;
}

.detail-container__body > .slick-track {
  height: 100%;
}

.detail-container__recommendations {
  margin: 40px auto 20px auto;

}

.detail-container__body__infos {
  width: 75%;
  background: #fff;
  box-sizing: border-box;
  border-radius: 15px;
  font-family: Inter;
